.item{
    width:200px;
    height: 50;
}
.item img{
    display: flex;
    height: 200px;
    width: 100%;
}
.item p{
    margin: 6px 0px;
    font-size: 12px;
}
.item-prices{
    display: flex;
    gap:20px;
}
.item-price-new{
    color: #374151;
    font-size: 18px;
    font-weight: 600;
}
.item-price-old{
    color: #8c8c8c;
    font-size:18px;
    font-weight: 500;
    text-decoration: line-through;
}

.item:hover{
    transform:scale(1.05);
    transition: 0.6s;
}
