.productdisplay{
    display: flex;
    margin: 20px 70px;
}

.productdisplay-left{
    display: flex;
    gap:8px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 8px;
}
.productdisplay-img-list img{
    height:75px;

}

.productdisplay-main-img{
    height: 300px;
    width: 300px;

}

.productdisplay-right{
    margin: 0px 50px;
    display: flex;
    flex-direction: column;
}
.productdisplay-right h1{
    color: #3d3d3d;
    font-size: 20px;
    font-weight: 700;
}
.productdisplay-right-stars{
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap: 5px;
    color: #1c1c1c;
    font-size: 16px;
}
.productdisplay-right-prices{
    display: flex;
    margin: 20px 20px;
    gap:10px;
    font-size: 24px;
    font-weight: 700;
}

.productdisplay-right-price-old{
   color: #818181; 
   text-decoration: line-through;
}

.productdisplay-right-price-new{
    color: #ff4141;
}
.productdisplay-right-size h1{
    margin-top:55px;
    color: #656565;
    font-size:20px;
    font-weight: 600;
}
.productdisplay-right-sizes{
    display: flex;
    margin: 30px 0px;
    gap: 20px;
}
.productdisplay-right-sizes div{
    
    justify-content: center;
    padding: 10px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius:3px;
    cursor:pointer;
}

.productdisplay-right-button{
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background: #ff4141;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;

}

.productdisplay-right-category{
    margin-top: 10px;
}
.productdisplay-right-category span{
    font-weight: 600;
} 

