.loginsignup{
    width: 100%;
    height: 88vh;
    background: #fce3fe;
    padding-top: 70px;
    margin-bottom: 40px;
}

.loginsignup-container{
    width: 380px;
    height: 500px;
    background: white;
    margin: auto;
    padding: 10px 60px;
}

.loginsignup-container h1{
    margin: 20px 0px;
    font-size: 30px;
}
.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 30px;
}

.loginsignup-fields input{
    height: 40px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 14px;

}

.loginsignup-container button{
    width: 100%;
    padding-left: 20px;
    /* width: px; */
    height: 40px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
    justify-content: center;
}

.loginsignup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 14px;
    font-weight: 500;
}
.loginsignup-login span{
    color: #ff4141;
    font-weight: 600;
}

.loginsignup-agree{
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap:20px;
    color: #5c5c5c;
    font-size: 14px;
    font-weight: 500;

}